import React from 'react';
import logo from '../assets/logo-min.png';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";
  import {Activity} from './Activity';
  import { Home } from './home';
  import {Messages} from './Messages';
  import {User} from './User';
  import {Explore} from './Explore';

export const Header = () => {
    return (<>
    <Router>
    <nav className="navbar hide-small" role="navigation">
            <div className="navbar-brand">
                <div className="navbar-item">
                    <img src={logo} width="100px"></img>
                </div>
                </div>
            <div className="navbar-search">
                <center>
                    <form>
                        <input type="text" defaultValue="Search"></input>
                    </form>
                </center>
            </div>
            <div className="navbar-icon">
                <center>
                <div className="navicons">
            <Link to='/'><i class="fas fa-home"></i></Link>
            </div>
            <div className="navicons">
            <Link to='/messages'>
            <i className="fab fa-facebook-messenger"></i>
                </Link>
            </div>
            <div className="navicons">
            <Link to='/explore'>
            <i class="fas fa-compass"></i>
            </Link>
            </div>
            <div className="navicons">
                <Link to='/activity'>
            <i class="fas fa-heart"></i>
                </Link>
            </div>
            <div className="navicons">
                <Link to="/@tanmay.1902">
                <div className="user">
                    <i class="fas fa-user"></i>
                </div>
                </Link>
            </div>
            <div className="clear"></div>
                </center>
            </div>
        </nav>
        <nav className="navbar hide-large">
            <center>
                <img src={logo} width="100px"></img>
            </center>
        </nav>
        <nav className="navbottom hide-large">
            <div className="navicons">
            <Link to='/'><i class="fas fa-home"></i></Link>
            </div>
            <div className="navicons">
            <Link to='/messages'>
            <i className="fab fa-facebook-messenger"></i>
                </Link>
            </div>
            <div className="navicons">
            <Link to='/explore'>
            <i class="fas fa-compass"></i>
            </Link>
            </div>
            <div className="navicons">
                <Link to='/activity'>
            <i class="fas fa-heart"></i>
                </Link>
            </div>
            <div className="navicons">
                <Link to="/@tanmay.1902">
                <div className="user">
                    <i class="fas fa-user"></i>
                </div>
                </Link>
            </div>
            <div className="clear"></div>
        </nav>
        <div className="padding"></div>
        
        <Switch>
          <Route path="/explore">
            <Explore />
          </Route>
          <Route path="/@tanmay.1902">
            <User />
          </Route>
          <Route path="/messages">
              <Messages />
          </Route>
          <Route path="/activity">
              <Activity />
          </Route>
          <Route path="/">
              <Home/>
          </Route>
        </Switch> 
    </Router>
    </>
    )
}
