import React from 'react'
import {Peop} from './sub/Peop';
import {
    BrowserRouter as Router,
    Switch,
    Route
  } from "react-router-dom";
import {Newmessage} from './sub/Newmessage';
export const Messages = () => {
    return (
        <>
        <Router>
        <Switch>
            <Route path="/messages">
        <div className="padding-low"></div>
        <div className="super-container hide-small">
            <div>
                <div className="users-prompt">
                <Peop/>
                </div>
                <div className="chat-section">
                <Newmessage/>
                </div>
                <div className="cleared"></div>
            </div>
        </div>

        <div class="hide-large contained">
            <Peop/>
        </div>
        </Route>
        </Switch>
        </Router>
        </>
    )
}
