import React from 'react'
import tel from '../../assets/msg.png';

export const Newmessage = () => {
    return (
        <div className="nochatbg">
          <center>
            <div className="telicon">
                <img src={tel} width="100px" height="100px"></img>
            </div>
            <div className="newmessage">
                <div className="thoda-big">
                    Your Messages
                </div>
                <div className="small">
                Send private photos and messages to a friend or group.
                </div>
            </div>
          </center> 
        </div>
    )
}
